	module.exports = {"data":{"post":{"html":"<p>My blog is schizophrenic, just like me. I sometimes blog about my family, sometimes about politics, sometimes about being an expat in a strange land, and sometimes about geeky computer stuff. Since the geeky computer stuff doesn't have much to do with the other stuff, and my mother doesn't really want to read the geeky stuff, I'm going to try putting it on a separate page called <a title=\"Posts not intended for my mother\" href=\"http://www.papascott.de/tech.php\">PapaTech</a>.    We'll see how that works.</p>","fields":{"title":"Posts not intended for my mother","date":"29 Mar 2004","url":"https://papascott.de/2004-03-29-posts-not-intended-for-my-mother/"}}},"pathContext":{"slug":"/2004-03-29-posts-not-intended-for-my-mother/","prev":{"fields":{"title":"Firesomething","date":"29 Mar 2004","slug":"/2004-03-29-firesomething/"}},"next":{"fields":{"title":"Engineer Genes","date":"30 Mar 2004","slug":"/2004-03-30-engineer-genes/"}}}}

